import { Link } from 'gatsby'
import React, { Component } from 'react'
if (typeof window !== `undefined`) {
  const M = require('materialize-css/dist/js/materialize')
}

export default class HeaderPopoutMenu extends Component {
  componentDidMount = () => M.AutoInit()

  render() {
    return (
      <ul className="sidenav" key="sideNavBar" id="sideNavBar">
        <li>
          <Link to="/">Home</Link>
        </li>
        <li>
          <Link to="/">About</Link>
        </li>
        <li>
          <Link to="/">Blog</Link>
        </li>
      </ul>
    )
  }
}
